.main-nav {
  color: @color-white;
  background-color: @color-blue;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
}

.main-nav__toggle {
  .button();
  width: 50px;
  height: 24px;
  background-color: rgb(245, 9, 9);
}

.main-nav__list {
  &--hide {
    display: none;
  }

  &--show {
    display: block;
  }
}
